export interface IProfile {
    id: string
    img: string
    first_name: string
    last_name: string
    login: string
}

const getProfile = async (): Promise<IProfile | null> => {
    const token = localStorage.getItem('access_token');
    if (!token) return null;
    try {
        const url = new URL('https://login.yandex.ru/info');
        url.searchParams.append('format', 'json');
        url.searchParams.append('oauth_token', token);
        const rawRes = await fetch(url);
        const res = await rawRes.json();
        return {
            id: res.id,
            img: `https://avatars.yandex.net/get-yapic/${res.default_avatar_id}/islands-200`,
            first_name: res.first_name,
            last_name: res.last_name,
            login: res.login,
        };
    } catch (e) {
        return null;
    }
};

export default getProfile;