import React, {useState} from 'react';
import {Button, useToast} from '@chakra-ui/react';
import {useNavigate} from 'react-router-dom';

const CLIENT_ID = 'f57ff11a6f094f2898a78992edf81e8f';
const REDIRECT_URI = 'https://91.203.66.65:80/auth';

const getYandexAuthUrl = () => {
    const url = new URL('https://oauth.yandex.ru/authorize');
    url.searchParams.append('response_type', 'token');
    url.searchParams.append('client_id', CLIENT_ID);
    url.searchParams.append('redirect_uri', REDIRECT_URI);
    url.searchParams.append('display', 'popup');
    return url.toString();
}

const YandexAuth: React.FC = () => {
    const navigate = useNavigate();
    const [loading, setLoading] = useState(false);
    const toast = useToast();

    const openAuthForm = () => {
        const token = localStorage.getItem('access_token');
        if (token) {
            navigate('home');
            return;
        }
        setLoading(true);
        const url = getYandexAuthUrl();
        const h = 650;
        const w = 550;
        const y = (window.screen.height - h) * 0.5;
        const x = (window.screen.width - w) * 0.5;
        const authWindow = window.open(url, 'popup', `width=${w},height=${h},top=${y},left=${x}`);
        if (authWindow) {
            const intervalID = setInterval(() => {
                if (!authWindow.closed) return;
                clearInterval(intervalID);
                setLoading(false);
                const token = localStorage.getItem('access_token');
                if (!token) {
                    toast({
                        title: '  ',
                        status: 'error',
                        isClosable: true,
                    });
                    return;
                }
                navigate('home');
            }, 1000);
        }
    };

    return <div>
        <Button
            size='lg'
            colorScheme='red'
            onClick={openAuthForm}
            isLoading={loading}
        >   </Button>
    </div>;
};

export default YandexAuth;