import React, {useEffect, useState} from 'react';
import {Link} from 'react-router-dom';
import {Box, Text, Image, Heading} from '@chakra-ui/react';

import getProfile, {IProfile} from '../yandex/api';

interface PropsTypes {
    children: React.ReactNode;
}

const MainLayout: React.FC<PropsTypes> = ({children}) => {
    const [data, setData] = useState<IProfile | null>(null);

    useEffect(() => {
        getProfile().then((res) => setData(res));
    }, []);

    return <Box>
        <Box
            bgColor='blue.600'
            h={100}
            display='flex'
            justifyContent='space-between'
            alignItems='center'
        >
            <Link to='/home'>
                <Heading as='h1' ml={8} color='white'></Heading>
            </Link>
            <Box display='flex' alignItems='center' pr={8}>
                {data && <>
                    <Image boxSize='20' mr={4} src={data.img} borderRadius={16} userSelect='none' />
                    <Text color='white' fontWeight='medium'>{data.first_name} {data.last_name}</Text>
                </>}
            </Box>
        </Box>
        <Box>
            {children}
        </Box>
    </Box>;
};

export default MainLayout;